import React from 'react';
import { Splide, SplideSlide } from '@splidejs/react-splide';
import '@splidejs/splide/dist/css/themes/splide-default.min.css';
//import './blue.css'


function Slide ()  {
	return (
		<div className="wrapper"  style={{gridArea: "slider" ,  zIndex:"2"}}  >
			<Splide
				options={ {
					type     : 'loop',
					height   : '300px',
					autoWidth: true,
					autoHeight: true,
					focus    : 'center',
					autoplay: true,
					
				} }
			>
				
					<SplideSlide >
					<img src={"ii.jpg"} alt={"Image 1"} width="600px" height="300px"/>
					</SplideSlide> 
					<SplideSlide >
					<img src={"1.jpg"} alt={"Image 1"}width="600px" height="300px"/>
					</SplideSlide> 
					<SplideSlide >
					<img src={"2.jpg"} alt={"Image 1"}width="600px" height="300px"/>
					</SplideSlide> 
					<SplideSlide >
					<img src={"3.jpg"} alt={"Image 1"}width="600px" height="300px"/>
					</SplideSlide> 
			</Splide>
		</div>
	);
} 
export default Slide;